        cloudA = gltf.scene.children.find(child => child.name === 'cloudA')
        cloudA.material = rimMaterial

      //  Add baked visuals to the scene
        scene.add(floor)
        scene.add(ground)
        scene.add(letters)
        scene.add(rim)

        //scene.add(cloudA)

