/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */

// Presets 
const camTop = {
    x: 0,
    y: 80,
    z: 0, 
    fov: 2
}

const camA = {
    x: 2,
    y: 3,
    z: 2,
    fov: 45
}

const camB = {
    x: -2,
    y: 3,
    z: 2,
    fov: 45
}

const camBack = {
    x: -2, 
    y: 3, 
    z: -2,
    fov: 45
}

//const myCam = camBack
//const myCam = camTop
//const myCam = camB
const myCam = camA

// Base camera
const camera = new THREE.PerspectiveCamera(myCam.fov, sizes.width / sizes.height, 0.1, 100)
camera.position.x = myCam.x
camera.position.y = myCam.y
camera.position.z = myCam.z
scene.add(camera)


// Controls
const controls = new OrbitControls(camera, canvas)
controls.enableDamping = true

// block camera orbit 
controls.enableRotate = true

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    antialias: true
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
renderer.outputEncoding = THREE.sRGBEncoding


/**
 * Animate
 */
const clock = new THREE.Clock()
let oldElapsedTime = 0


let quaternion = new THREE.Quaternion();

const tick = () =>
{
    const elapsedTime = clock.getElapsedTime()
    const deltaTime = elapsedTime - oldElapsedTime
    oldElapsedTime = elapsedTime
    //console.log(deltaTime)

    // Update controls
    controls.update()

    // Update Physics world
    // After 2 seconds
    if (elapsedTime > 2.0) {
        world.step(1/60, deltaTime, 3)
    }


    // update sphere to match physics
    sphere.position.copy(sphereBody.position)
    //console.log(sphere.position)


    /** 
    if (sphereBody.position.z < -0.4 || sphereBody.position.z > 0.4) {
        
    } else {
        
        if (sphereBody.position.x < -0.8 || sphereBody.position.x > 0.8) {
            //console.log('X out')
        } else {
            //console.log('X in')
            sphere.position.y += 0.1
        }
    }
    */

    // Dummy floor     

        //floorBody.quaternion.setFromEuler(- cursor.x - 0.3, cursor.y, 0)
        floorBody.quaternion.setFromEuler(-Math.PI / 3 - cursor.x, cursor.y - 0.5, 0)
        quaternion = floorBody.quaternion
        //console.log(quaternion)

        collidersLetter_i.quaternion.setFromEuler(-Math.PI / 3 - cursor.x, cursor.y - 0.5 , 1)
        collidersLetter_h.quaternion.setFromEuler(-Math.PI / 3 - cursor.x, cursor.y - 0.5 , 1.2)
        collidersLetter_y.quaternion.setFromEuler(-Math.PI / 3 - cursor.x, cursor.y - 0.5 , 0.6)
        
        

        if (floor) {
          floor.rotation.x = (Math.PI * 0.3 - cursor.x) - 0.419
          floor.rotation.z = 0.5 - cursor.y
         
        }//console.log(quaternion)

        if (ground){
            ground.rotation.x = Math.PI / 3 - cursor.x - 0.53
            ground.rotation.z = 0.5 - cursor.y 
        }

     

        if (letters) {
            letters.rotation.x = Math.PI / 3 - cursor.x - 0.552
            letters.rotation.z = 0.5 - cursor.y
            letters.rotation.y
        }

       if (rim) {
        rim.rotation.x = (Math.PI * 0.3 - cursor.x) - 0.419
        rim.rotation.z = 0.5 - cursor.y
        
       }
   
       //camera.lookAt(sphere.position)
      //console.log(sphere.position.x)
   
    
    /**
     * Physics debugger
     */
    //cannonDebugger.update()

    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()


